import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Inject,
  Input,
  LOCALE_ID,
  OnInit,
  Optional,
  Output,
  Renderer2,
  Self,
  ViewChild
} from '@angular/core';
import {NgControl} from '@angular/forms';
import {trigger} from '@angular/animations';
import type {WeekDay} from '@angular/common';
import {ENTER, SPACE, TAB} from '@angular/cdk/keycodes';
import {Directionality} from '@angular/cdk/bidi';
import {asapScheduler, timer} from 'rxjs';
import {take, takeUntil} from 'rxjs/operators';
import {isArray, isFunction, isNil, pick} from 'lodash-es';

import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularCustomEventType,
  AdaptRadarAngularService
} from '../../common/radar/adapt-radar';
import {AdaptInteractionSourceService, getInteractionSource} from '../../common/interaction-source';
import {getDateParts} from './common/partition.utils';
import {AdaptRxFeedbackComponent, AdaptRxFormControlDirective} from '../form-control/index';
import {PlacementArray} from '../../common/positioning';
import {AdaptDeviceDetectionService} from '../../common/device-detection.service';
import {AdaptTranslateService} from '../../common/i18n/index';
import {AdaptDropdownCloseReason, AdaptDropdownDirective, AdaptDropdownMobileView} from '../../dropdown/index';
import {SafeCdr} from '../../common/safe-cdr';
import {getEventKeyCode} from '../../common/key-codes';
import {getFocusableElements} from '../../common/focus';
import {
  SLIDE_ANIMATION,
  checkIfPropsDefined,
  getDatetimeModelPropsByMode,
  checkIsAfter,
  getStructWithDefaults
} from './common/common.utils';

import {RxDateStruct, RxDatetimePickerMode, RxDatetimeStruct, RxDatetimeTexts} from './adapt-rx-datetime.types';
import {AdaptRxDatetimeConfigService} from './adapt-rx-datetime-config.service';
import {AdaptRxDatetimeAdapter} from './adapt-rx-datetime-adapter.service';
import {AdaptRxDatetimeI18nService} from './adapt-rx-datetime-i18n.service';
import {AdaptRxDatetimeParserFormatter} from './adapt-rx-datetime-parser-formatter.service';
import {AdaptRxDatetimePickerComponent} from './datetime-picker/adapt-rx-datetime-picker.component';
import {FocusOrigin} from '@angular/cdk/a11y';

@Component({
  selector: 'adapt-rx-datetime-range',
  templateUrl: './adapt-rx-datetime-range.component.html',
  preserveWhitespaces: false,
  animations: [
    trigger('slideAnimation', SLIDE_ANIMATION)
  ]
})
export class AdaptRxDatetimeRangeComponent extends AdaptRxFormControlDirective implements OnInit {
  protected controlName: string = 'datetime-range';

  /**
   * Placeholder attribute
   * @docs-default
   */
  @Input() placeholder: string;

  /**
   * Inline mode
   * @docs-default false
   */
  @Input() inline: boolean = false;

  /**
   * The preferred placement of the typeahead
   * @docs-default auto
   */
  @Input() placement: PlacementArray = 'auto';

  /**
   * Attach popup to the body
   * @docs-default false
   */
  @Input() appendToBody: boolean = false;

  /**
   * Light theme for inline mode
   * @docs-default false
   */
  @Input() inlineLight: boolean = true;

  /**
   * Small size for inline mode
   * @docs-default false
   */
  @Input() inlineCompact: boolean = false;

  /**
   * Day filter
   * @docs-default
   */
  @Input() dayFilter: (date: RxDateStruct) => boolean;

  /**
   * Trigger for disable picker's wizard
   * @docs-default false
   */
  @Input() disableWizard: boolean = false;

  /**
   * Picker's mode
   * @docs-default RxDatetimePickerMode.Date
   */
  @Input() mode: RxDatetimePickerMode = RxDatetimePickerMode.Date;

  /**
   * Enables seconds for time
   * @docs-default false
   */
  @Input() hasSeconds: boolean = false;

  /**
   * Enables/disables 12-hours time format.
   * By default time format calculates automatically
   * @docs-default
   */
  @Input() set use12HoursTime(value: boolean) {
    this._use12HoursTime = value;
  }

  get use12HoursTime(): boolean {
    return !isNil(this._use12HoursTime) ? this._use12HoursTime : this._datetimeI18nService.getTimeFormat(this.localeID) === 12;
  }

  /**
   * Calendar first day of week.
   * By default calculates automatically
   * @docs-default
   */
  @Input() set firstDayOfWeek(value: WeekDay) {
    this._firstDayOfWeek = value;
  }

  get firstDayOfWeek(): WeekDay {
    return !isNil(this._firstDayOfWeek) ? this._firstDayOfWeek : this._datetimeI18nService.getFirstDayOfWeek(this.localeID);
  }

  /**
   * Initial datetime uses when there is model
   * @docs-default
   */
  @Input() initialDatetime: RxDatetimeStruct;

  /**
   * Callback that returns default datetime struct to fill the empty struct properties emitted by the picker
   * @docs-default
   * @since v12.11.0
   */
  @Input() defaultDatetime: (mode?: RxDatetimePickerMode, hasSeconds?: boolean) => RxDatetimeStruct;

  /**
   * Use disable styles for readonly state
   * @docs-default false
   * @since v10.5.0
   */
  @Input() disabledStyleForReadonlyState: boolean = false;

  /**
   * Custom class for the dropdown menu
   * @docs-default
   * @since 10.13.0
   */
  @Input() popupClass: string;

  /**
   * Datetime texts
   * @docs-default
   */
  @Input()
  get texts(): RxDatetimeTexts {
    return this._texts;
  }

  set texts(value: RxDatetimeTexts) {
    this._texts = {...this._defaultTexts, ...value};
  }

  /**
   * Input format
   * @docs-default
   * @since v12.12.0
   */
  @Input() inputFormat: string;

  /* eslint-disable @angular-eslint/no-output-on-prefix */
  /**
   *  An event fired when the popup is opened or closed
   */
  @Output() readonly onPopupOpenChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  /**
   * An event fired when model change
   */
  @Output() readonly onDatetimeChange: EventEmitter<RxDatetimeStruct[]> = new EventEmitter<RxDatetimeStruct[]>();
  /* eslint-enable @angular-eslint/no-output-on-prefix */

  /**
   * Reference to input control
   */
  @ViewChild('inputControlRef', {static: false}) inputControlRef: ElementRef<HTMLInputElement>;

  /**
   * Reference to popup open button
   */
  @ViewChild('openPopupRef', {static: false}) openPopupRef: ElementRef<HTMLInputElement>;

  /**
   * Reference to AdaptRxFeedbackComponent
   */
  @ViewChild('adaptRxFeedbackRef', {static: true}) adaptRxFeedbackRef: AdaptRxFeedbackComponent;

  /**
   * Start range tab
   */
  @ViewChild('startRangeTab', {static: false}) startRangeTab: ElementRef<HTMLElement>;

  /**
   * End range tab
   */
  @ViewChild('endRangeTab', {static: false}) endRangeTab: ElementRef<HTMLElement>;

  /**
   * Reference to AdaptDropdownDirective
   */
  @ViewChild('adaptDropdownDirective', {static: false}) popup: AdaptDropdownDirective;

  /**
   * Reference to start AdaptRxDatetimePickerComponent
   */
  @ViewChild('startDatetimePickerComponentRef', {static: false}) startDatetimePickerComponent: AdaptRxDatetimePickerComponent;

  /**
   * Reference to end AdaptRxDatetimePickerComponent
   */
  @ViewChild('endDatetimePickerComponentRef', {static: false}) endDatetimePickerComponent: AdaptRxDatetimePickerComponent;

  /**
   * Reference to picker menu container
   */
  @ViewChild('datetimePickerContainer', {static: false}) datetimePickerContainer: ElementRef<HTMLElement>;

  PICKER_MODE: typeof RxDatetimePickerMode = RxDatetimePickerMode;

  inputValue: string = '';
  mobileViewConfig: AdaptDropdownMobileView = {
    isPicker: true
  };
  isMobile: boolean = false;
  inputRangeSeparator: string = ' - ';
  isLastWizardStep: boolean = false;
  startRangeActive: boolean = true;
  startRangeActiveAnimation: number = 1;
  rangeFrom: RxDatetimeStruct = null;
  rangeTo: RxDatetimeStruct = null;
  rangeFromDayFilter: (date: RxDateStruct) => boolean = this._rangeFromDayFilter.bind(this);
  rangeToDayFilter: (date: RxDateStruct) => boolean = this._rangeToDayFilter.bind(this);
  startValueSummary: { date: string; time: string; dateTime: string } = {date: '', time: '', dateTime: ''};
  endValueSummary: { date: string; time: string; dateTime: string } = {date: '', time: '', dateTime: ''};
  startPickerActiveDatetime: RxDatetimeStruct = null;
  endPickerActiveDatetime: RxDatetimeStruct = null;
  defaultInitialDatetime: RxDatetimeStruct;
  isRtl: boolean = false;

  get controlStartValue(): RxDatetimeStruct {
    return this._datetimeAdapter.fromModel(this._controlStartValue);
  }

  set controlStartValue(value: RxDatetimeStruct) {
    const nextValue = isFunction(this.defaultDatetime) ?
      getStructWithDefaults(value, this.defaultDatetime(this.mode, this.hasSeconds)) : value;

    this._controlStartValue = this._datetimeAdapter.toModel(nextValue);
    this.startPickerActiveDatetime = this.controlStartValue;
  }

  get controlEndValue(): RxDatetimeStruct {
    return this._datetimeAdapter.fromModel(this._controlEndValue);
  }

  set controlEndValue(value: RxDatetimeStruct) {
    const nextValue = isFunction(this.defaultDatetime) ?
      getStructWithDefaults(value, this.defaultDatetime(this.mode, this.hasSeconds)) : value;

    this._controlEndValue = this._datetimeAdapter.toModel(nextValue);
    this.endPickerActiveDatetime = this.controlEndValue;
  }

  get isSecondsWrap(): boolean {
    return this.mode === RxDatetimePickerMode.DateTime && !this.inline && this.hasSeconds;
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  private _controlStartValue: any = null;
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  private _controlEndValue: any = null;
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  private _backupControlStartValue: any = null;
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  private _backupControlEndValue: any = null;
  private _use12HoursTime: boolean;
  private _firstDayOfWeek: WeekDay;
  private _defaultTexts: RxDatetimeTexts;
  private _texts: RxDatetimeTexts;
  private _popupFocusableElements: HTMLElement[];
  private _openWithKeyboard: boolean = false;
  private _targetOpen: HTMLInputElement | HTMLButtonElement;

  constructor(@Optional() @Self() ngControl: NgControl,
              _datetimeConfigService: AdaptRxDatetimeConfigService,
              _deviceDetectionService: AdaptDeviceDetectionService,
              private _datetimeI18nService: AdaptRxDatetimeI18nService,
              // TODO: Refactor this if possible
              /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
              private _datetimeAdapter: AdaptRxDatetimeAdapter<any>,
              private _datetimeParserFormatter: AdaptRxDatetimeParserFormatter,
              _changeDetectorRef: ChangeDetectorRef,
              private _renderer: Renderer2,
              public ts: AdaptTranslateService,
              @Inject(LOCALE_ID) public localeID: string,
              _adaptRadarService: AdaptRadarAngularService,
              private _interactionSourceOriginService: AdaptInteractionSourceService,
              private _dir: Directionality,
              _elem: ElementRef<HTMLElement>) {
    super(ngControl, ts, _adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptRxDatetimeRange, _changeDetectorRef);

    this.isMobile = _deviceDetectionService.isMobile();
    this._texts = this._defaultTexts = _datetimeConfigService.texts;
    this.isRtl = this._dir.value === 'rtl';
  }

  ngOnInit(): void {
    super.ngOnInit();

    if (this.inline) {
      this._updateRange();
      this._checkWizardLastStep();
    }

    this._updateInputValue();
  }

  switchToStartDT(): void {
    this.startRangeActive = true;
    this.startRangeActiveAnimation--;
    this._checkWizardLastStep();
  }

  switchToEndDT(): void {
    this.startRangeActive = false;
    this.startRangeActiveAnimation++;
    this._checkWizardLastStep();
  }

  datetimeOpenHandler(event: MouseEvent | KeyboardEvent): void {
    if (!this.nonInteractive && !this.popup.isOpen()) {
      const interactionSource: FocusOrigin = getInteractionSource(event);
      const keyCode: number = event ? getEventKeyCode(event as KeyboardEvent) : null;

      this._targetOpen = event ? (event.target as HTMLInputElement | HTMLButtonElement) : null;

      if (this.isMobile && this._targetOpen === this.inputControlRef.nativeElement) {
        this._onTouched();
      }

      if (interactionSource === 'keyboard' && keyCode !== ENTER && keyCode !== SPACE) {
        return;
      }

      this._openWithKeyboard = interactionSource === 'keyboard';
      this.popup.open(event);
    }
  }

  onInput(value: string): void {
    this.inputValue = value;

    const propsToPick = getDatetimeModelPropsByMode(this.mode, this.hasSeconds);
    const splitString = value.split(this.inputRangeSeparator.trim());

    // Start value
    const parsedStartDatetime =
      this._datetimeParserFormatter.parse(splitString[0]?.trim(), this.mode, this.hasSeconds, this.use12HoursTime, this.localeID, this.inputFormat);

    if (!parsedStartDatetime) {
      this.controlStartValue = null;

    } else if (parsedStartDatetime.parseError) {
      this.controlStartValue = parsedStartDatetime;

    } else {
      this.controlStartValue = Object.assign({
        year: null,
        month: null,
        date: null,
        hours: null,
        minutes: null,
        seconds: null
      }, pick(parsedStartDatetime, propsToPick));
    }

    // End value
    const parsedEndDatetime =
      this._datetimeParserFormatter.parse(splitString[1]?.trim(), this.mode, this.hasSeconds, this.use12HoursTime, this.localeID, this.inputFormat);

    if (!parsedEndDatetime) {
      this.controlEndValue = null;

    } else if (parsedEndDatetime.parseError) {
      this.controlEndValue = parsedEndDatetime;

    } else {
      this.controlEndValue = Object.assign({
        year: null,
        month: null,
        date: null,
        hours: null,
        minutes: null,
        seconds: null
      }, pick(parsedEndDatetime, propsToPick));
    }

    this._updateRange();

    if (this.popup.isOpen()) {
      this._checkWizardLastStep();
    }

    this._propagateChange();
  }

  blurHandler(event: FocusEvent): void {
    super.blurHandler(event);

    if (!this.popup.isOpen()) {
      this._updateInputValue();
      this._onTouched();
    }
  }

  /**
   * Popup toggle handler
   */
  popupOpenChangeHandler(isOpen: boolean, closeReason?: AdaptDropdownCloseReason): void {
    this.onPopupOpenChange.emit(isOpen);

    if (isOpen) {
      this.defaultInitialDatetime = getDateParts(new Date());

      this.popup.popupAnimationDone
        .pipe(takeUntil(this.destroy$), take(1))
        .subscribe(() => {
          this._popupFocusableElements = getFocusableElements(this.datetimePickerContainer.nativeElement);

          if (this._openWithKeyboard) {
            this._popupFocusableElements[0].focus();
          }
        });

      this._backupControlStartValue = this._controlStartValue;
      this._backupControlEndValue = this._controlEndValue;
      this.switchToStartDT();
      this._updateRange();
      this._checkWizardLastStep();

    } else {
      if (document.activeElement !== this.inputControlRef.nativeElement) {
        this._updateInputValue();
      }

      this._onTouched();

      if (closeReason === AdaptDropdownCloseReason.Esc && this._targetOpen) {
        this._targetOpen.focus();
      }
    }
  }

  datetimePickerContainerKeydown(event: KeyboardEvent): void {
    if (getEventKeyCode(event) === TAB) {
      if (event.shiftKey) {
        if (event.target === this._popupFocusableElements[0]) {
          event.preventDefault();
          this.popup.close();
        }
      } else {
        if (event.target === this._popupFocusableElements[this._popupFocusableElements.length - 1]) {
          event.preventDefault();
          this.popup.close();
        }
      }
    }
  }

  pickerCloseAndCancel(): void {
    this._controlStartValue = this._backupControlStartValue;
    this._controlEndValue = this._backupControlEndValue;
    this.startPickerActiveDatetime = this.controlStartValue;
    this.endPickerActiveDatetime = this.controlEndValue;
    this._propagateChange();
    this._updateInputValue();
    this.popup.close();
    this.inputControlRef.nativeElement.focus();
  }

  pickerClearAll(): void {
    this._controlStartValue = null;
    this._controlEndValue = null;
    this.startPickerActiveDatetime = null;
    this.endPickerActiveDatetime = null;

    this._updateRange();
    this._propagateChange();
    this._updateInputValue();
    this._checkWizardLastStep();
  }

  goToNextStep(): void {
    if (this.isLastWizardStep) {
      this.popup.close();
      this.inputControlRef.nativeElement.focus();
    } else {
      if (this.startRangeActive) {
        if (this.startDatetimePickerComponent.publicApi.isLastStep()) {
          this.switchToEndDT();
          this.endRangeTab.nativeElement.focus();
        } else {
          this.startDatetimePickerComponent.publicApi.nextStep();
          this._checkWizardLastStep();
        }
      } else {
        if (this.endDatetimePickerComponent.publicApi.isLastStep()) {
          this.switchToStartDT();
          this.startRangeTab.nativeElement.focus();
        } else {
          this.endDatetimePickerComponent.publicApi.nextStep();
          this._checkWizardLastStep();
        }
      }
    }
  }

  onStartPickerValueChange(newValue: RxDatetimeStruct): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptRxDatetime, {
      GroupName: this.adaptRadarGroupName,
      EventType: AdaptRadarAngularCustomEventType.ValueChanged,
      Label: this.label,
      Required: this.required,
      ReadOnly: this.readonly,
      Disabled: this.disabled,

      Type: this.mode,
      Range: true,
      Inline: this.inline,
      // InputMethod: getInteractionSourceOrigin(event, this.isMobile)
      /*DynamicEntry: boolean;*/
    });

    this.controlStartValue = newValue;

    this._updateRange();

    this.onDatetimeChange.emit([this._controlStartValue, this._controlEndValue]);
    this._propagateChange();
    this._updateInputValue();

    if (this.inline) {
      this._onTouched();
    } else {
      this._checkWizardLastStep();
    }
  }

  onEndPickerValueChange(newValue: RxDatetimeStruct): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptRxDatetime, {
      GroupName: this.adaptRadarGroupName,
      EventType: AdaptRadarAngularCustomEventType.ValueChanged,
      Label: this.label,
      Required: this.required,
      ReadOnly: this.readonly,
      Disabled: this.disabled,

      Type: this.mode,
      Range: true,
      Inline: this.inline,
      /*InputMethod: getInteractionSourceOrigin(event, this.isMobile)*/
      /*DynamicEntry: boolean;*/
    });

    this.controlEndValue = newValue;

    this._updateRange();

    this.onDatetimeChange.emit([this._controlStartValue, this._controlEndValue]);
    this._propagateChange();
    this._updateInputValue();

    if (this.inline) {
      this._onTouched();
    } else {
      this._checkWizardLastStep();
    }
  }

  onPickerStateChange(): void {
    if (!this.inline) {
      // To be sure the picker view already have painted
      timer(0, asapScheduler)
        .pipe(takeUntil(this.destroy$))
        .subscribe(() => {
          this._checkWizardLastStep();
        });
    }
  }

  focusHandler(event: FocusEvent): void {
    super.focusHandler(event);

    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptRxDatetime, {
      GroupName: this.adaptRadarGroupName,
      EventType: AdaptRadarAngularCustomEventType.Focus,
      Label: this.label,
      Required: this.required,
      ReadOnly: this.readonly,
      Disabled: this.disabled,

      Type: this.mode,
      Range: true,
      Inline: this.inline,
      InputMethod: this._interactionSourceOriginService.getLastInteractionSourceOrigin()
      /*DynamicEntry: boolean;*/
    });
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  writeValue(value: any): void {
    if (isArray(value)) {
      this.controlStartValue = this._datetimeAdapter.fromModel(value[0]);
      this.controlEndValue = this._datetimeAdapter.fromModel(value[1]);
    } else {
      this.controlStartValue = null;
      this.controlEndValue = null;
    }

    this._updateRange();
    this._updateInputValue();

    if (this.popup?.isOpen()) {
      this._checkWizardLastStep();
    }

    SafeCdr.detectChanges(this._changeDetectorRef);
  }

  protected _focusNativeElement(): void {
    if (!this.inline) {
      this.inputControlRef.nativeElement.focus();
    }
  }

  private _isModelFull(value: RxDatetimeStruct): boolean {
    return checkIfPropsDefined(value, getDatetimeModelPropsByMode(this.mode, this.hasSeconds));
  }

  private _updateInputValue(): void {
    this.startValueSummary = this._getDateTimeSummary(this.controlStartValue);
    this.endValueSummary = this._getDateTimeSummary(this.controlEndValue);

    const rangeFrom = this.startValueSummary ? this.startValueSummary.dateTime : '';
    const rangeTo = this.endValueSummary ? this.endValueSummary.dateTime : '';

    this.inputValue = rangeFrom + (rangeFrom || rangeTo ? this.inputRangeSeparator : '') + rangeTo;
  }

  private _getDateTimeSummary(control: RxDatetimeStruct): { date: string; time: string; dateTime: string } {
    if (!this._isModelFull(control)) {
      return null;
    }

    return {
      date: this._datetimeParserFormatter.format(control, RxDatetimePickerMode.Date, false, false, this.localeID, this.inputFormat),
      time: this._datetimeParserFormatter.format(control, RxDatetimePickerMode.Time, this.hasSeconds, this.use12HoursTime, this.localeID, this.inputFormat),
      dateTime: this._datetimeParserFormatter.format(control, this.mode, this.hasSeconds, this.use12HoursTime, this.localeID, this.inputFormat)
    };
  }

  private _updateRange(): void {
    this.rangeFrom = this.controlStartValue;
    this.rangeTo = this.controlEndValue;
  }

  private _checkWizardLastStep(): void {
    // In PROD mode this.endDatetimePickerComponent is undefined
    // Add extra change detection to prevent fix it
    // https://github.bmc.com/bmc-ux/adapt-angular/issues/6721
    SafeCdr.detectChanges(this._changeDetectorRef);

    this.isLastWizardStep = this.disableWizard ||
      (this._isModelFull(this.controlStartValue) &&
        this._isModelFull(this.controlEndValue) &&
        !this.startRangeActive &&
        this.endDatetimePickerComponent?.publicApi.isLastStep());
  }

  private _rangeToDayFilter(date: RxDatetimeStruct): boolean {
    return (this.rangeFrom ? checkIsAfter(date, this.rangeFrom, true) : true) &&
      (isFunction(this.dayFilter) ? this.dayFilter(date) : true);
  }

  private _rangeFromDayFilter(date: RxDatetimeStruct): boolean {
    return (this.rangeTo ? checkIsAfter(this.rangeTo, date, true) : true) &&
      (isFunction(this.dayFilter) ? this.dayFilter(date) : true);
  }

  private _propagateChange(): void {
    this._onChange(this._controlStartValue || this._controlEndValue ? [this._controlStartValue, this._controlEndValue] : null);
  }
}
